<template>
<!--  <div class="pageForm" id="privilegeId" :style="'height: ' +(fullHeight-40) + 'px; width: '+ (fullWidth- 380)+'px' ">-->
    <div id="privilegeId">

    <h3 style="float: left; margin-left: 30px">Windows提权辅助</h3>
    <a-divider/>

    <div style="width: 280px; float:left; margin-left: 2%">
      <a-form-model
          ref="ruleForm"
          :model="kb_form"
          :rules="rules"

      >
        <a-form-model-item ref="values" label="" prop="values">
          <a-textarea style="overflow: auto"
                      v-model="kb_form.values" @blur="() => {$refs.values.onFieldBlur();}"
                      placeholder="[01]: KB2360937
[02]: KB2478960
[03]: KB2507938
[04]: KB2501138"
                      :auto-size="{ minRows: 6, maxRows: 6 }"
          />
        </a-form-model-item>
      </a-form-model>
      <div style="float: left; margin-left: 30px; margin-top: -12px; text-align: left; line-height: 250%;">
        <a-cascader
            :options="options"
            :show-search="{ filter }"
            placeholder="系统版本选择"
            @change="SelectonChange"
            :value="systemSelect"
        />
        <a-checkbox @change="PrivlegeonChange">
          权限提升
        </a-checkbox>
        <a-checkbox @change="RceonChange">
          远程代码执行
        </a-checkbox>
      </div>
    </div>

    <div style="float: right; width: 300px; text-align: left;margin-right: 2% ">
      <span>说明:</span>
      <br>
      在Windows提权的时候，将补丁相关的内容复<br/>制
      到框内， 可对比补丁查找可利用方式。

      <br>
      将<font color="red">补丁号</font>或<font color="red">systeminfo</font>信息复制到文本栏，如:

      <div style="text-indent: 2em; line-height: 20px; margin-top: 8px;">
        <span><font color="red">[01]: KB2360937</font></span><br>
        <span style="margin-left: 28px"><font color="red">[02]: KB2478960</font></span><br>
        <span style="margin-left: 28px"><font color="red">[03]: KB2507938</font></span><br>
        <span style="margin-left: 28px"><font color="red">[04]: KB2501138</font></span><br>
      </div>

      <a-button type="danger" @click.native="kb_search" style="margin-top: 10px; margin-left: 200px"> 提交</a-button>

    </div>

    <div style="margin-top: 300px">
      <a-table :columns="columns" :data-source="data" class="components-table-demo-nested" :pagination="false"
               :style="'border-collapse: collapse; margin: 10px 10px; margin-top: -10px'" rowKey="ms_title">

            <span slot="ms_type" slot-scope="ms_type">
              {{ ms_type| ellipsis }}
            </span>

        <span slot="ms_status" slot-scope="text,record">
              <a-tag v-if="record.use_exp === null || record.use_exp ===''">EXP</a-tag>
              <a-tag v-else color="green">EXP</a-tag>
              <a-tag v-if="record.patch_info === '' || record.patch_info ===null">补丁</a-tag>
              <a-tag v-else color="magenta">补丁</a-tag>
            </span>

        <span slot="patch_num" slot-scope="text, record">
              <div v-if="record.patch_num === '' || record.patch_num === null">{{ record.patch_num }}</div>
              <div v-else>KB{{ record.patch_num }}</div>
            </span>

        <p slot="expandedRowRender" slot-scope="data" style="margin-left: -40px; width: 100%; word-break:keep-all">
          <a-descriptions title="" size="small" bordered class="msdesc" style="width: 105%">
            <a-descriptions-item label="漏洞名称" :span="3">
              {{ data.ms_title }}
            </a-descriptions-item>
            <a-descriptions-item label="漏洞描述" :span="3">
              {{ data.ms_desc }}
            </a-descriptions-item>
            <a-descriptions-item label="影响版本" :span="3">
              <span style="white-space: pre-line">{{ data.affected_version }}</span>
            </a-descriptions-item>
            <a-descriptions-item label="利用EXP" :span="3">
              {{ data.use_exp }}
            </a-descriptions-item>
            <a-descriptions-item label="补丁信息" :span="3">
              {{ data.patch_info }}
            </a-descriptions-item>
          </a-descriptions>

        </p>
      </a-table>

    </div>



<!--      <div id="components-back-top-demo-custom">-->
<!--        <a-back-top :visibilityHeight="500" :target="backtopTarget">-->
<!--          &lt;!&ndash;              <div class="ant-back-top-inner">UP</div>&ndash;&gt;-->
<!--        </a-back-top>-->
<!--      </div>-->

      <a-back-top :visibilityHeight="200"></a-back-top>

  </div>
</template>




<script>
// import Antd from 'ant-design-vue'
const columns = [
  {title: '漏洞编号', dataIndex: 'ms_title', key: 'ms_title', width: 200},
  {title: 'KB编号', dataIndex: 'patch_num', key: 'patch_num', scopedSlots: {customRender: 'patch_num'}},
  {title: '利用类型', dataIndex: 'ms_type', key: 'ms_type', scopedSlots: {customRender: 'ms_type'}},
  {title: '状态', dataIndex: 'ms_status', key: 'ms_status', align: 'center', scopedSlots: {customRender: 'ms_status'}},
]
const data = []

for (let i = 0; i < 0; ++i) {
  data.push({
    key: i,
    ms_num: '',
    patch_num: '',
    ms_type: '',
    ms_status: '',

    ms_title: '',
    ms_desc: '',
    affected_version: '',
    use_exp: '',
    patch_info: '',
  })
}

// 下拉列表
const innerColumns = [
  {title: '漏洞名称', dataIndex: 'ms_num', key: 'ms_num', scopedSlots: {colSpan: 0}},
  {title: '漏洞描述', dataIndex: 'ms_desc', key: 'ms_desc'},
  {title: '影响版本', dataIndex: 'affected_version', key: 'affected_version'},
  {title: '利用EXP', dataIndex: 'use_exp', key: 'use_exp'},
  {title: '补丁信息', dataIndex: 'patch_info', key: 'patch_info'},
]

export default {
  name: 'Privilege',
  data() {
    return {
      // 提权辅助展示列及数据
      data,
      columns,
      innerColumns,

      // 勾选筛选rce按钮的状态
      rce_status: '',
      // 勾选筛选提权按钮的状态
      privilege_stauts: '',
      // 操作系统筛选值
      systemSelect: [''],

      // 提权辅助输入信息验证规则
      kb_form: {
        values: '',
      },

      rules: {
        values: [
          {required: true, message: '填写内容不能为空', trigger: 'blur'},
        ],

      },
      options: [
        {
          value: 'Windows 10',
          label: 'Windows 10',
        },
        {
          value: 'Windows 8',
          label: 'Windows 8',
        },
        {
          value: 'Windows 7',
          label: 'Windows 7',
        },
        {
          value: 'Windows XP',
          label: 'Windows XP',
        },
        {
          value: 'Windows Server 2012',
          label: 'Windows Server 2012',
        },
        {
          value: 'Windows Server 2008',
          label: 'Windows Server 2008',
        },
      ],

      fullHeight: document.documentElement.clientHeight,
      fullWidth: document.documentElement.clientWidth,



    }
  },
  filters: {
    ellipsis(value) {
      if (!value) return ''
      if (value === 'Elevation of Privilege') {
        return '提权'
      }
      return 'RCE'
    },
  },

  mounted() {

  },

  methods: {

    // 返回顶部的定位dom函数
    backtopTarget() {
      return document.getElementById('privilegeId')
    },

    // 勾选权限提升的按钮
    PrivlegeonChange(e) {
      // console.log(`checked = ${e.target.checked}`)
      this.privilege_stauts = e.target.checked
    },

    // 勾选rce的按钮
    RceonChange(e) {
      // console.log(e.target.checked)
      this.rce_status = e.target.checked
    },
    // 提权辅助功能
    kb_search() {

      this.$refs.ruleForm.validate(valid => {
        if (!valid) {
          return
        } else {
          if (this.systemSelect[0] === '' || this.systemSelect[0] === undefined) {
            this.$message.info('系统信息必须选择')
            return
          }
          // console.log(this.systemSelect)
          this.$http.post('/privilegeSupport',
              {
                kb_num: this.kb_form.values,
                rce: this.rce_status,
                privilege: this.privilege_stauts,
                system_info: this.systemSelect[0],
              }).then(res => {
            if (res.data.status === false) {
              this.$message.error(res.data.msg)
            } else {
              this.data = res.data.msg
              // console.log(this.data)
            }
          })

        }
      })
    },

    // 操作系统勾选的系统选项
    // SelectonChange(value, selectedOptions) {
    SelectonChange(value) {
      // console.log(value, );
      this.systemSelect = [value[0]]
      // console.log(this.systemSelect)
    },

    filter(inputValue, path) {
      return path.some(option => option.label.toLowerCase().indexOf(inputValue.toLowerCase()) > -1)
    },
  },
  // components : {Antd},
  created() {
    this.overtimer = window.setInterval(() => {
      setTimeout(() => {
        // console.log(document.documentElement.clientHeight, "privilege ")
        this.fullHeight = document.documentElement.clientHeight
        this.fullWidth = document.documentElement.clientWidth
      })

    }, 1000)
    this.$once('hook:beforeDestroy', () => {
      clearInterval(this.overtimer);
      this.overtimer = null;
    })

  },

}
</script>


<style scoped>

.pageForm {
  margin-top: 20px;
  height: 635px;
  display: block;
  min-height: 200px;
  width: 620px;
  padding-top: 24px;
  position: relative;
  background: #fff;
  -webkit-box-shadow: 0 2px 2px rgb(0 0 0 / 8%), 0 1px 8px rgb(0 0 0 / 12%);
  box-shadow: 0 2px 2px rgb(0 0 0 / 8%), 0 1px 8px rgb(0 0 0  / 12%);
  border-radius: 4px;
  overflow: auto;
  text-align: center;
  margin-left: 30px;
  /*background-color: #333333;*/
  float: left;
}


.msdesc >>> .ant-descriptions-item-content {
  border-right: 1px solid #1d1a1a;
  border-left: 1px solid #1d1a1a;
  border-bottom: 1px solid #1d1a1a;
  border-top: 1px solid #1d1a1a;

}

.msdesc >>> .ant-descriptions-item-label {
  border-right: 1px solid #1d1a1a;
